zeOptions: {\n                type: Array,\n                default () {\n                    return [10, 20, 50, 100];\n                }\n            },\n            selectedIndex: {\n                type: Number,\n                default: 0\n            },\n            pagenum: {\n                type: Number,\n                twoWay: true,\n                default: 0\n            },\n            totalSize: {\n                type: Number,\n                default: 0\n            }\n        },\n        ready () {\n            var vm = this;\n            var el = $(vm.$el);\n            el.pagination({\n                mode: vm.mode,\n                pagesize: vm.pagesize,\n                pageSizeOptions: vm.pageSizeOptions,\n                selectedIndex: vm.selectedIndex,\n                pagenum: vm.pagenum,\n                totalSize: vm.totalSize\n            });\n\n            el.on('pagersearch', (e, pagenum, pagesize, total) => {\n                vm.$dispatch('page-change', pagenum, pagesize, total);\n            });\n\n            vm.$watch('totalSize', (newVal) => {\n                el.pagination('destroy');\n                el.pagination({\n                    mode: vm.mode,\n                    pagesize: vm.pagesize,\n                    pageSizeOptions: vm.pageSizeOptions,\n                    selectedIndex: vm.selectedIndex,\n                    pagenum: vm.pagenum,\n                    totalSize: newVal\n                });\n            });\n        },\n        beforeDestroy () {\n            var el = $(this.$el);\n            el.off('pagersearch');\n        }\n    };\n</script>\n\n<style>\n    .bh-pager .bh-pager-label {\n        text-indent: 10px;\n        line-height: 26px;\n    }\n</style>\n"],"sourceRoot":"webpack://"}]);

