         position: {\r\n                type: String,\r\n                default: 'default'\r\n            },\r\n            clickClose: {\r\n                type: Boolean,\r\n                default: false\r\n            },\r\n            showFooter: {\r\n                type: Boolean,\r\n                default: false\r\n            },\r\n            closeText: {\r\n                default: '我知道了'\r\n            }\r\n        },\r\n        ready () {\r\n            initTooltip(this);\r\n\r\n            this.$watch('target', (newVal) => {\r\n                initTooltip(this);\r\n            });\r\n        },\r\n        beforeDestroy () {\r\n            this.destroy();\r\n        },\r\n        methods: {\r\n            /**\r\n             * 关闭提示框\r\n             */\r\n            close () {\r\n                if (this.target) {\r\n                    $(this.target).jqxTooltip('close');\r\n                }\r\n            },\r\n            destroy () {\r\n                if (this.target) {\r\n                    $(this.target).jqxTooltip('destroy');\r\n                }\r\n            }\r\n        }\r\n    };\r\n</script>\r\n\r\n<style scoped>\r\n    footer > div {\r\n        cursor: pointer;\r\n    }\r\n</style>"],"sourceRoot":"webpack://"}]);

